main {
  width: 100%;
  height: 100vh;
  display: flex;
}
.modal, main {
  background-position: center;
  background-size: cover;
}
.centerfy,
.centerfy-col {
  display: flex;
  justify-content: center;
  align-items : center;
}
.centerfy-col {
  flex-direction: column;
}
.app {
  width: 320px;
  min-height: 400px;
  background-color: #b95d5d61 !important;
  margin: auto;
  border-radius: 14px;
  position: relative;
  text-align: center;
  padding: 12px 12px 1% 12px;
  justify-content: space-between;
  background-position: center;
  background-size: cover;
  background-blend-mode: multiply;
  box-shadow: 9px 10px 17px #000000b8;
}
.form {
  height : 26vh;
  margin-top : 20px;
  justify-content : space-evenly;
  border-radius: 0px 0px 8px 8px ;
  width : 100%;
}
.form label {
  font-size: 22px;
  font-weight: 700;
  color: #e91e63;
  background-color: #000000ad;
  width: 94%;
  padding: 1%;
}
.btns {
  width : 100%;
  height: 50px;
  justify-content: space-evenly;
}
.form button ,
.option-btn{
  width: 120px;
  height: 40px;
  line-height: 38px;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  border: 3px solid #e91e63b0;
  background-color: rgb(246, 41, 75);
  font-size: 18px;
  font-style: italic;
  font-weight: 600;
  color :white;
  transition: all 0.6s ease-in-out;
}
.start {
  background-color: rgb(128, 0, 128) !important;
}
.option-btn {
  width: 146px !important;
  height: 40px !important;
  line-height: 42px;
  font-size: 15px;
}
.form input {
  height: 35px;
  outline: none;
  border: none;
  border-bottom: 3px solid #e91e63ad;
  text-align: center;
  font-size: 18px;
  color: purple;
  font-weight: 600;
  width: 100%;
  background-color: #ffffffc2;
}
.idea-container {
  width: 100%;
  padding: 1% 0%;
  background-color: purple;
  border-radius: 8px 8px 0px 0px;
  color : white;
}
.idea-container h2 {
  margin: 5px 0px;
  padding-bottom: 7px;
  border-bottom: 2px solid #f63355;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.close {
  font-size : 26px;
  color : red;
  font-weight : 900;
  cursor : pointer;
}
.modal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 68vh;
  z-index: 100;
  border: 2px  solid red;
  background-color: rgb(0 0 0 / 97%);
  color: white;
  font-size: 16px;
  font-weight: 900;
  justify-content: space-between;
}
.modal .header, .modal .footer {
  height: 20%;
  background-color: aqua;
  width : 100%;
}
.modal h6 {
  text-decoration-line: underline;
  text-decoration-color: rgb(222, 23, 56);
  text-decoration-style: solid;
  text-decoration-thickness: 5px;
}
.modal h4 {
  text-decoration-line: underline;
  text-decoration-color: rgb(23 222 209);
  text-decoration-style: wavy;
  text-decoration-thickness: 4px;
  font-size: 26px;
}
.modal-content {
  background-color: #000000d1;
  height: 60%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.option-btn {
  position: absolute;
  bottom: 115px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 30px;
  outline: none;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}




.form button:hover ,
.option-btn:hover {
  background-color: rgba(246, 41, 75, 0.523);
  letter-spacing: 1.8px;
  font-weight: 800;
}
.start:hover {
  background-color: rgba(128, 0, 128, 0.585) !important;
}
.option-btn:hover {
  background-color: rgb(78 235 255) !important;
  color: rgb(0 0 0) !important;
  font-weight: 900;
}
.preloader {
  width: 100%;
  height: 100vh;
  background-position: center center;
  background-size: cover;
  background-color: #f6000052;
  background-blend-mode: hue;
  position: fixed;
  top: 0px;
  left: 0px;
  display :flex;
 
}

.preloader article {
  width: 60%;
  height: 50vh;
  justify-content: space-evenly;
  background-color: #000000a1;
  margin: auto;
  color: white;
  text-align: center;
  position: relative;
}
.preloader article h1 {
  font-size: 28px;
  font-family: castellar;
  letter-spacing: 1px;
  line-height: 25px;
  font-weight: 700;
  font-style: oblique;
  background-color: #d3719f87;
  padding: 6% 5px;
  margin-top: -47px;
}
.preloader article h1 span {
  margin-left : 8px;
  color : red;
  font-weight: 900;
  font-size: 30px;
}
.preloader article p {
  font-size: 27px;
  margin-top: -54px;
  font-family: monospace;
  letter-spacing: -0.5px;
  text-decoration: underline;
  font-style: oblique;
}
.button-wrapper  {
  width : 100%;
  height : 10vh;
  background-color: black;
  display:flex;
  position: absolute;
  bottom: 0px;
  left:0px;
}
.button-wrapper button {
  margin: auto;
  color: white;
  width: 129px;
  background-color: #800038;
  height: 40px;
  border-radius: 40px;
  padding: 0px 5px;
  cursor: pointer;
  transition : all 0.5s ease-in-out;
}
.button-wrapper button:hover {
  background-color: #8000374f;
}
/*===============================
  RESPONSIVE SCREENS
=================================*/
@media(mx-width : 780px){
  .preloader article {
    width: 680px;
  }
}

@media(mx-width : 700px){
  .preloader article {
    width: 580px;
  }
}

@media(mx-width : 600px){
  .preloader article {
    width: 380px;
  }
}

@media(mx-width : 400px){
  .preloader article {
    width: 380px;
  }
}

@media(mx-width : 380px){
  .preloader article {
    width: 98%;
  }
}